export default{
	data:function(){
		return {
			oldWord:"",
			keyword:"",
			addrList:[],
			localAddress:"",
			aaBoxClass:"",
			searchList:"",
			nearList:"",
			lat:"",
			lng:"",
			city:"厦门",
		}
	},
	onLoad:function(){
		var city=this.app.getCity();
		if(city){
			this.city=city;
		}
		this.userAddrList();
		this.localAddress=this.app.getLocalAddress();
		if(this.localAddress==''){
			this.getLocation();
		}
		
		this.getNearList();
		
	},
	onShow:function(){
		var city=this.app.getCity();
		if(city){
			this.city=city;
		}
	},
	methods:{
		gourl:function(url){
			uni.navigateTo({
				url:url
			})
		},
		searchShow:function(){
			this.aaBoxClass="flex-col";
		},
		search:function(){
			var that=this;
			var latlng=this.app.getGps();
			setTimeout(function(){
				if(that.oldWord!=that.keyword){
					that.oldWord=that.keyword;
					that.app.get({
						url:that.app.apiHost+"/index.php?m=lbs&keyword="+that.keyword,
						data:{
							lat:latlng.lat,
							lng:latlng.lng,
							city:that.city
						},
						success:function(res){
							that.searchList=res.data
						}
					}) 
				}
			},10)
			
			
		},
		userAddrList:function(){
			var that=this;
			that.app.get({
				url:that.app.apiHost+"/index.php?m=user_address&a=my&ajax=1",
				success:function(res){
					that.addrList=res.data.list;
				}
			})
		},
		getNearList:function(){
			var that=this;
			var latlng=this.app.getGps();
			that.app.get({
				url:that.app.apiHost+"/index.php?m=lbs&a=near",
				data:{
					lat:latlng.lat,
					lng:latlng.lng,
					city:that.city
				},
				success:function(res){
					that.nearList=res.data
				}
			}) 
		},
		
		setGpsLocal:function(item){
			this.localAddress=item.address;
			this.app.setLocalAddress(item.address);
			this.app.setGps(item.lat,item.lng);
			uni.reLaunch({
				url:"../../pages/index/index"
			});
		},
		setLocation:function(addr){
			this.localAddress=addr;
			this.app.setLocalAddress(addr);
			uni.reLaunch({
				url:"../../pages/index/index"
			});
		},
		getLocation:function(){
			var that=this;
			uni.getLocation({
				type: 'wgs84',
				success: function (res) {
					that.lat=res.latitude;
					that.lng=res.longitude;
				},
				fail:function(e){
					console.log(e)
				}
			});
		}	
	}
}
